/* Add a background color to the body */
body {
    background-color: #121212; /* Dark background for the entire page */
    color: white; /* Default text color */
    margin: 0; /* Remove default margin */
    font-family: Arial, sans-serif; /* Set a default font */
  }
  
  .homepage-container {
    padding-top: 50px; /* Reduced padding for better alignment */
    display: flex;
    gap: 20px; /* Space between the grid and details */
    padding: 20px;
    flex-wrap: wrap; /* Wrap content on smaller screens */
    background-color: #1c1c1c; /* Background for the homepage container */
    border-radius: 10px; /* Rounded corners for the container */
  }
  
  .movies-grid {
    display: grid;
    grid-template-columns: repeat(
      auto-fill,
      minmax(250px, 1fr)
    ); /* Responsive grid */
    gap: 35px; /* Space between movie cards */
    flex: 4; /* Allow grid to take more space */
  }
  
  .movie-card {
    cursor: pointer; /* Pointer cursor on hover */
    text-align: center; /* Center text in card */
    transition: transform 0.3s; /* Smooth scale transition */
  }
  
  .movie-card img {
    width: 100%; /* Full width of the card */
    border-radius: 10px; /* Rounded corners */
    object-fit: cover; /* Cover to prevent distortion */
  }
  
  .movie-card h2 {
    font-size: 18px; /* Font size for movie title */
    margin: 10px 0 0 0; /* Top margin only */
  }
  
  .movie-card:hover {
    transform: scale(1.05); /* Scale up on hover */
  }
  
  .movie-details-container {
    flex: 1; /* Take up remaining space for movie details */
    max-width: 350px; /* Maximum width for details */
    padding: 20px; /* Padding inside details */
    background-color: #2c3e50; /* Dark background for details */
    color: white; /* Text color */
    border-radius: 10px; /* Rounded corners */
  }
  
  @media (max-width: 768px) {
    .homepage-container {
      flex-direction: column; /* Stack elements vertically */
    }
  
    .movie-details-container {
      max-width: 100%; /* Full width for small screens */
      margin-top: 20px; /* Space above details */
    }
  }
  /* Pagination styles */
  .pagination {
    display: flex;
    justify-content: center; /* Center buttons horizontally */
    margin-top: 20px; /* Space above buttons */
  }
  
  .pagination button {
    padding: 10px 15px; /* Padding for buttons */
    margin: 0 10px; /* Space between buttons */
    background-color: #1abc9c; /* Button color */
    color: white; /* Button text color */
    border: none; /* Remove border */
    border-radius: 5px; /* Rounded corners */
    cursor: pointer; /* Pointer on hover */
    transition: background-color 0.3s; /* Smooth background transition */
  }
  
  .pagination button:disabled {
    background-color: #7f8c8d; /* Grey color for disabled state */
    cursor: not-allowed; /* Not-allowed cursor */
  }
  
  .pagination button:hover:not(:disabled) {
    background-color: #16a085; /* Darker shade on hover */
  }
  